import {mapState,mapMutations} from 'vuex';
import {SHIBIE_IMG,SHIBIE_OBJ,SHIBIE_TYPE} from '../../store/mutation-types.js'
export default {
	data() {
		return {
			
		}
	},
	computed:{
		...mapState([
			'requesturl',
			'key'
		])
	},
	methods: {
		...mapMutations([
			SHIBIE_IMG,
			SHIBIE_OBJ,
			SHIBIE_TYPE
		]),
		toolsecondpage:function(e){
			var type = e.currentTarget.dataset.type;
			var _this = this
			console.log(type);
			var url = ''
			if(type == 'qushuiyin'){
				url = 'qushuiyin/qushuiyin'
				uni.navigateTo({
					url:url
				})
			}else if(type == 'zhiwu' || type == 'chongwu'){
				_this.SHIBIE_TYPE(type)
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album','camera'], //从相册选择
					success: res => {
					uni.showLoading({
						title:'图片识别中',
						mask:true
					})
					_this.SHIBIE_IMG(res.tempFiles[0].path)
					console.log('dasdsasadada',res)
					uni.getFileSystemManager().readFile({
							filePath: res.tempFiles[0].path, //选择图片返回的相对路径
							encoding: 'base64', //编码格式
							success: res => { //成功的回调
								console.log('data:image/png;base64,' + res.data)
								var imgurl = 'data:image/png;base64,' + res.data
								console.log(imgurl)
								var url = (type == 'zhiwu' ? '/txapi/scanplant/index' : '/txapi/scanpet/index')
								uni.request({
									method:'POST',
									url:_this.requesturl + url,
									header: { 'Content-Type': 'application/x-www-form-urlencoded'},
									data:{
										key:_this.key,
										img:imgurl
									},
									success:function(res){
										console.log(res)
										if(res.data.newslist){
											if(type == 'zhiwu'){
												var zhiwuobj = res.data.newslist[0]
												_this.SHIBIE_OBJ(zhiwuobj)
												uni.hideLoading()
												uni.navigateTo({
													url:'zhiwu/zhiwu'
												})
											}else{
												var chongwuobj = res.data.newslist[0]
												_this.SHIBIE_OBJ(chongwuobj)
												
												uni.navigateTo({
													url:'zhiwu/zhiwu'
												})
											}
										}else{
											uni.hideLoading()
											uni.showToast({
												title:'失败了，请近距离拍摄',
												icon:'none'
											})
										}
									}
								})
							}
						})
					}
				})
			}else if(type == 'tgrj'){
				uni.navigateTo({
					url:'../tgrj/tgrj'
				})
			}else if(type == 'xzpd'){
				uni.navigateTo({
					url:'xzpd/xzpd'
				})
			}else if(type == 'historytoday'){
				uni.navigateTo({
					url:'historytoday/historytoday'
				})
			}
		}
	}
}
